body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background: #f4f7fa;
  color: #333;
}

.App {
  max-width: 960px;
  margin: 2rem auto;
  padding: 0 1rem 4rem;
}

header {
  text-align: center;
  margin-bottom: 2rem;
}

header h1 {
  color: #007acc;
  margin-bottom: 0.3rem;
}

header p {
  font-size: 1.1rem;
  color: #555;
}

.category {
  background: #fff;
  padding: 1rem 1.5rem;
  margin-bottom: 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.05);
}

.category h2 {
  margin-bottom: 1rem;
  color: #007acc;
}

.course-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 1rem;
}

.course-card {
  background: #e9f0fb;
  border-radius: 10px;
  padding: 1rem;
  box-shadow: 0 1px 5px rgba(0, 122, 204, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 150px;
}

.course-card label {
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  user-select: none;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.course-title {
  flex-grow: 1;
}

.course-price {
  margin-top: 0.5rem;
  font-weight: 700;
  font-size: 1.1rem;
  color: #004a99;
}

.buy-btn {
  margin-top: 0.5rem;
  background-color: #007acc;
  color: white;
  border: none;
  border-radius: 6px;
  padding: 0.4rem 0;
  cursor: pointer;
  font-weight: 700;
  font-size: 0.9rem;
  transition: background-color 0.3s ease;
}

.buy-btn:hover {
  background-color: #005a99;
}

.cart {
  background: #fff;
  padding: 1rem 1.5rem;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.05);
  margin-bottom: 2rem;
  text-align: center;
}

.cart h2 {
  margin-bottom: 1rem;
  color: #007acc;
}

.buy-all-btn {
  background-color: #28a745;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 0.7rem 1.2rem;
  cursor: pointer;
  font-weight: 700;
  font-size: 1rem;
  transition: background-color 0.3s ease;
  margin-top: 1rem;
}

.buy-all-btn:hover {
  background-color: #1e7e34;
}

.contact {
  text-align: center;
  margin-top: 3rem;
  font-size: 1.1rem;
  color: #555;
}

footer {
  text-align: center;
  margin-top: 3rem;
  font-size: 0.9rem;
  color: #888;
}
